.hero{
	width: 100%;
	height: 100%;
	position: relative;
}

.heroMid{
	width: 100%;
	height: 70vh !important;
	position: relative;
}

.heroMid h1{
	padding: 4rem !important;
}

.heroMid img{
	height: 70vh;
}


img{
	width: 100%;
	height: 100vh;
	object-fit: cover;
}

.hero-text{
	position: absolute;
	top: 55%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
}

.hero-text h1{
	font-size: 3rem;
	font-weight: 800;
	background: #fff;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}

.hero-text p{
	font-size: 1.3rem;
	color: #fff;
	padding: 0.5rem 0 2rem 0;
}

.hero-text .show{
	text-decoration: none;
	background: #fff;
	padding: 1rem 2rem;
	border-radius: 6px;
	font-size: 1.1rem;
	font-weight: bold;
	letter-spacing: 2px;
	color: #000;
}

.hero-text .hide{
	display: none;
}

@media screen and (max-width:555px){
	.hero-text h1{
		padding: 10px 20px;
	}

	.hero-text p{
		font-size: 1.1rem;
		padding: 0 0 2rem 0;
	}

	.hero-text .show{
		font-size: 1rem;
		padding: .6rem 1.1rem;
	}
}